body {
  counter-reset: day-counter 0;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.calendar {
  grid-template-columns: repeat(7, 100px);
  gap: 1px;
  width: 706px;
  display: grid;
}

.calendar__day {
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 30px;
  transition: transform .5s, background-color .5s;
  display: flex;
  position: relative;
}

.calendar__day:hover {
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

.calendar__day:nth-child(-n+31):before {
  content: counter(day-counter);
  counter-increment: day-counter;
}

.calendar--start-sun .calendar__day:first-child {
  grid-column: 1;
}

.calendar--start-mon .calendar__day:first-child {
  grid-column: 2;
}

.calendar--start-tue .calendar__day:first-child {
  grid-column: 3;
}

.calendar--start-wed .calendar__day:first-child {
  grid-column: 4;
}

.calendar--start-thu .calendar__day:first-child {
  grid-column: 5;
}

.calendar--start-fri .calendar__day:first-child {
  grid-column: 6;
}

.calendar--start-sat .calendar__day:first-child {
  grid-column: 7;
}
